@import 'tooltip';
@import 'toggle';
@import 'modal';

.peachpay {
	$columns: 12;

	@for $i from 1 through $columns {
		.col-#{$i} {
			width: calc(($i / 12) * 100%);
		}
	}

	.row {
		width: 100%;
		display: flex;
		flex-direction: row;
		gap: 0.5rem;
	}

	.v-align-middle {
		display: flex;
		align-items: center;
	}

	.h-align-middle {
		display: flex;
		justify-content: center;
	}

	.center {
		text-align: center;
	}

	.flex-row {
		display: flex;
		flex-direction: row;
	}

	.flex-col {
		display: flex;
		flex-direction: column;
	}

	.jc-end {
		justify-content: flex-end;
	}

	.jc-center {
		justify-content: center;
	}

	.jc-sb {
		justify-content: space-between;
	}

	.ai-center {
		align-items: center;
	}

	.gap-4 {
		gap: 4px;
	}

	.gap-8 {
		gap: 8px;
	}

	.gap-12 {
		gap: 12px;
	}

	.gap-24 {
		gap: 24px;
	}

	.w-100 {
		width: 100%;
	}

	.h-100 {
		height: 100%;
	}

	.flex-wrap {
		flex-wrap: wrap;
	}

	.hide {
		display: none !important;
	}

	img[src=''],
	img[src='#'] {
		content: url('https://www.martinezcreativegroup.com/wp-content/uploads/2014/05/img-placeholder.png');
	}

	dl.inline-dl {
		width: 100%;
		padding: 0;
		margin: 0;

		dt {
			float: left;
			width: calc(35% - 1rem);
			padding: 0 1rem 0 0;
			margin: 0 0 0.4rem;
			text-align: right;
			font-weight: 400;
		}

		dd {
			float: left;
			width: 65%;
			padding: 0;
			margin: 0 0 0.4rem;
			text-align: left;
		}
	}

	hr {
		margin: 10px 0;
		border: unset;
		border-top: 0.5px solid #e6e6e6;
	}

	/* icons */

	.arrow-top-right {
		mask-image: url('../../../public/img/arrow-top-right.svg');
		mask-repeat: no-repeat;
		mask-position: center;
		mask-size: contain;
	}

	.info-icon {
		mask-image: url('../../../public/img/info.svg');
		mask-repeat: no-repeat;
		mask-position: center;
		mask-size: contain;
	}

	/* icon sizes */

	.icon-18 {
		width: 18px;
		height: 18px;
		padding: 2px;
	}

	.icon-20 {
		width: 20px;
		height: 20px;
		padding: 2px;
	}

	.icon-24 {
		width: 24px;
		height: 24px;
		padding: 2px;
	}
}
