.pp-popup-mousemove-trigger {
	position: relative;
	text-anchor: middle;
}

.pp-tooltip-no-hover {
	position: relative;
}

.pp-tooltip-popup {
	width: 152px;
	background-color: #757575;
	border-width: 1px;
	border-style: solid;
	border-color: rgb(0 0 0 / 23%);
	color: white;
	text-align: center;
	white-space: normal !important;
	border-radius: 6px;
	line-height: 1.3;
	padding: 10px;
}

.pp-tooltip-popup::after {
	content: '';
	position: absolute;
	top: 100%;
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: #757575 transparent transparent;
}

.pp-tooltip-popup.pp-popup-right::after {
	top: 50%;
	left: -10px;
	margin-left: 0;
	margin-top: -5px;
	border-color: transparent #757575 transparent transparent;
}

.pp-tooltip-popup.pp-popup-below::after {
	bottom: 100%;
	top: revert;
	left: 24px;
	border-color: transparent transparent #757575;
}

.pp-popup-above {
	transform: translate3d(-50%, calc(-100% - 8px), 0);
	transform-origin: center;
}

.pp-popup-below {
	margin-top: 4px;
}

.pp-popup {
	visibility: hidden;
	opacity: 0;
	z-index: 1000000;
	position: fixed;
	height: fit-content;
	pointer-events: none;
	transition: all 0.2s, top 0s, left 0s !important;
}

.pp-popup.shown {
	visibility: visible !important;
	opacity: 1;
	pointer-events: all !important;
}
