#peachpay-gateway-details {
	dl {
		font-size: 0.85rem;

		dt {
			font-weight: 600;
		}

		dd {
			margin-inline-start: unset;
			color: rgb(109 109 109);
		}
	}

	h1 {
		display: inline-flex;
		align-items: center;

		span:has(img) {
			height: 42px;
		}
	}
}

.peachpay-gateway-icons {
	display: flex;
	gap: 0.2rem;
	margin-left: 0.4rem;
	align-items: flex-end;

	img {
		max-height: 1.6rem;
	}
}
