@import '../tokens/colors', '../tokens/typography','../scss/variants';

$button-sizes: "large" 12px 24px 8px, "medium" 8px 16px 6px, "small" 6px 12px 4px;

.button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    cursor: pointer;
    text-decoration: none;
	transition: all .3s;
}

@each $name,
$textDefault,
$textDefaultOnSurface,
$bgDefault,
$borderDefault,
$textInteractive,
$bgInteractive,
$borderInteractive,
$bgLight in $variants {
    .button-#{$name} {
        &-filled {

            @each $size,
            $v-padding,
            $h-padding,
            $gap in $button-sizes {
                &-#{$size} {
                    @extend .button;
                    background-color: $bgDefault;
                    color: $textDefaultOnSurface;
                    border: 1px solid $borderDefault;

                    &:hover,
                    &:active,
                    &:focus {
                        background-color: $bgInteractive;
                        color: $textDefaultOnSurface;
                    }

                    svg {
                        fill: $textDefaultOnSurface;
                    }

                    @if $size ==large {
                        @include orchLabelDefaultLarge;
                    }

                    @else if $size ==medium {
                        @include orchLabelDefaultMedium;
                    }

                    @else if $size ==small {
                        @include orchLabelDefaultSmall;
                    }

                    padding: $v-padding $h-padding;
                    gap: $gap;
                }
            }
        }

        &-outlined {

            @each $size,
            $v-padding,
            $h-padding,
            $gap in $button-sizes {
                &-#{$size} {
                    @extend .button;
                    background-color: inherit;
                    border: 1px solid $borderDefault;
                    color: $textDefault;

                    &:hover,
                    &:active,
                    &:focus {
                        border-color: $borderInteractive;
                        color: $textInteractive;
                        background-color: $bgLight;
                    }

                    svg {
                        fill: $textDefault;

                        &:hover,
                        &:active,
                        &:focus {
                            fill: $textInteractive;
                        }
                    }

                    @if $size ==large {
                        @include orchLabelDefaultLarge;
                    }

                    @else if $size ==medium {
                        @include orchLabelDefaultMedium;
                    }

                    @else if $size ==small {
                        @include orchLabelDefaultSmall;
                    }

                    padding: $v-padding $h-padding;
                    gap: $gap;
                }
            }
        }

        &-text {

            @each $size,
            $v-padding,
            $h-padding,
            $gap in $button-sizes {
                &-#{$size} {
                    @extend .button;
                    background-color: inherit;
                    border: 1px solid transparent;
                    color: $textDefault;

                    &:hover,
                    &:active,
                    &:focus {
                        color: $textInteractive;
                        background-color: $bgLight;
                    }

                    svg {
                        fill: $textDefault;

                        &:hover,
                        &:active,
                        &:focus {
                            fill: $textInteractive;
                        }
                    }

                    @if $size ==large {
                        @include orchLabelDefaultLarge;
                    }

                    @else if $size ==medium {
                        @include orchLabelDefaultMedium;
                    }

                    @else if $size ==small {
                        @include orchLabelDefaultSmall;
                    }

                    padding: $v-padding $h-padding;
                    gap: $gap;
                }
            }
        }
    }
}

/* DISABLED */

$disabled-variants: "filled"$orchColorLightTextDisabled $orchColorLightBgDisabled none,
    "outlined"$orchColorLightTextDisabled inherit $orchColorLightBorderDisabled,
    "text"$orchColorLightTextDisabled inherit none;

@each $name,
$text,
$bg,
$border in $disabled-variants {
    .disabled-#{$name} {
        color: $text;
        background-color: $bg;
        border-color: $border;

        svg {
            fill: $orchColorLightTextDisabled;

            &:hover,
            &:active,
            &:focus {
                fill: $orchColorLightTextDisabled;
            }
        }

        &:hover,
        &:active,
        &:focus {
            color: $text;
            background-color: $bg;
            border-color: $border;
        }

        
    }
}


/* IF DEFAULT WHEN NOT INTERACTIVE */


$default-variants: "filled"$orchColorLightTextBasicDefault $orchColorLightBgBasicDefault transparent,
    "outlined"$orchColorLightTextBasicDefault inherit $orchColorLightBorderBasicDefault,
    "text"$orchColorLightTextBasicDefault inherit transparent;


@each $name,
$text,
$bg,
$border in $default-variants {
    .default-#{$name} {
        color: $text;
        background-color: $bg;
        border-color: $border;

        svg {
            fill: $orchColorLightTextBasicDefault;
        }
    }
}

/* FILL */

.fill {
    width: 100%;
}