.peachpay {
	.help-container {
		display: flex;

		.tutorial-button {
			display: flex;
			align-items: center;
			align-content: center;
		}

		.help-link {
			display: inline-flex;
			flex-wrap: nowrap;
			align-items: center;
			text-decoration: none;
		}

		.tutorial-text {
			color: #575757;
			padding-left: 5px;
			transition: color 0.3s;
			margin: 0;

			@media screen and (max-width: 1344px) {
				font-size: 12px;
			}
		}

		.help-img {
			opacity: 0.8;
			transition: opacity 0.3s;

			@media screen and (max-width: 1344px) {
				height: 16px;
			}
		}

		&:hover {
			.tutorial-text {
				color: #212121;
			}

			.help-img {
				opacity: 1;
			}
		}
	}
}
