@keyframes animate-add {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@mixin set-button-color($color) {
	&:not(.notice-dismiss),
	&.notice-dismiss::before {
		background-color: $color;
	}

	&:nth-of-type(2) {
		&:not(.notice-dismiss),
		&.notice-dismiss::before {
			background-color: transparent;
			color: $color;
			font-weight: 500;
		}
	}

	&.notice-dismiss:only-of-type:hover::before,
	&:not(.notice-dismiss):first-of-type:hover {
		background-color: adjust-color($color, $lightness: 5%) !important;
	}

	&:not(.notice-dismiss):nth-of-type(2):hover,
	&.notice-dismiss:nth-of-type(2):hover::before {
		background-color: rgba($color, 0.1) !important;
	}
}

.peachpay-notices-container {
	position: fixed !important;
	height: fit-content;
	left: 24px;
	bottom: 24px;
	z-index: 20000;
	margin: 0 24px 0 160px;
	display: flex;
	flex-direction: column-reverse;
	transition: all .3s;

	@media screen and (max-width: 960px) {
		margin-left: 36px;
	}

	@media screen and (max-width: 782px) {
		margin-left: 0;
	}
}

.notice[id*="peachpay"] {
	width: fit-content;
	max-height: 200px;
	padding: 12px 24px 12px 24px !important;
	border-radius: 8px;
	display: none;
	justify-content: flex-end;
	align-items: flex-end;
	gap: 8px;
	transition: all .3s;

	&:has(.pp-banner-buttons) {
		flex-wrap: wrap;
	}
	
	// floating banners
	&[id*="peachpay_message"] {
		margin: 8px 56px 0 0;
		max-width: 790px;
	}

	// not floating banners
	&:not([id*="peachpay_message"]) {
		width: 100%;
	}

	&.notice-success {
		background-color: $orchColorLightBgSuccessExtralight;
		border: 1px solid $orchColorLightBorderSuccessDefault;
		border-left: 8px solid $orchColorLightBorderSuccessDefault;

		p::before {
			background-image: url(../../../public/img/check-circle-outline.svg);
		}

		button {
			@include set-button-color($orchColorLightBgSuccessDefault);
		}
	}

	&.notice-warning {
		background-color: $orchColorLightBgWarningExtralight;
		border: 1px solid $orchColorLightBorderWarningDefault;
		border-left: 8px solid $orchColorLightBorderWarningDefault;

		p::before {
			background-image: url(../../../public/img/warning-circle-outline.svg);
			margin-top: 3px;
			margin-bottom: 5px;
		}

		button {
			@include set-button-color($orchColorLightBgWarningDefault);
		}

		a.pp-notice-button {
			background-color: $orchColorLightBgWarningDefault;
			padding: 6px 12px;
			border: 1px solid transparent;
			border-radius: 8px;
			text-decoration: none;
			color: white;
			font-weight: 500;
			font-size: 12px;
		}
	}

	&.notice-error {
		background-color: $orchColorLightBgErrorExtralight;
		border: 1px solid $orchColorLightBorderErrorDefault;
		border-left: 8px solid $orchColorLightBorderErrorDefault;

		p::before {
			background-image: url(../../../public/img/error-circle-outline.svg);
		}

		button {
			@include set-button-color($orchColorLightBgErrorDefault);
		}
	}

	&.notice-info {
		background-color: $orchColorLightBgInfoExtralight;
		border: 1px solid $orchColorLightBorderInfoDefault;
		border-left: 8px solid $orchColorLightBorderInfoDefault;

		p::before {
			background-image: url(../../../public/img/info-circle-outline.svg);
		}

		button {
			@include set-button-color($orchColorLightBgInfoDefault);
		}
	}

	&.addition {
		animation: animate-add .3s ease-in -.1s;
		opacity: 1;
		display: flex;
	}

	&.removal {
		display: flex;
		max-height: 0;
		opacity: 0;
		transition: all .15s ease-in;
	}

	&>* {
		display: flex;
		top: unset;
		right: unset;
		bottom: unset;
		left: unset;
		position: relative;
		padding: 0;
		margin: 0 !important;
	}

	& * {
		transition: all .3s;
	}

	p {
		flex-grow: 1;

		&::before {
			content: '';
			width: 24px;
			height: 24px;
			border: 2px solid transparent;
			display: flex;
			flex-shrink: 0;
			box-sizing: border-box;
			margin: 4px 8px 4px 0px;
			background-position: center;
			background-repeat: no-repeat;
			background-size: contain;
		}
	}

	strong {
		display: flex;
		align-items: center;
		font-size: 14px;
		line-height: 24px;
		font-weight: 400;
		min-height: 32px;
	}

	.pp-banner-buttons {
		display: flex;
		gap: 8px;
	}

	button {
		color: white;
		cursor: pointer;
		background: 0 0;
		padding: 0;
		position: relative;
		
		&:not(.notice-dismiss),
		&.notice-dismiss::before {
			color: white;
			display: flex;
			position: relative;
			width: fit-content;
			height: unset;
			white-space: nowrap;
			align-self: center;
			font: inherit;
			border: 1px solid transparent;
			font-size: 13px;
			line-height: 18px;
			-webkit-font-smoothing: unset;
			font-weight: 500;
			font-size: 12px;
			border-radius: 8px;
			padding: 6px 12px;
			transition: all .3s;
		}

		&.notice-dismiss::before {
			content: "Close";
		}

		&:first-of-type {
			margin-left: 8px !important;
		}
	}
}


@media screen and (max-width: 782px) {
	.notice[id*="peachpay"] {
		padding: 10px 16px !important;
		gap: 12px 6px;

		p::before {
			width: 22px;
			height: 22px;
			margin-top: 3px;
			margin-bottom: 3px;
		}

		strong {
			min-height: 26px;
			font-size: 13px;
			line-height: 20px;
		}

		button, button::before {
			line-height: 14px !important;
			font-size: 11px !important;
		}
	}
}
