/* Top navigation */
#peachpay-nav {
	display: flex;
	flex-direction: column;
	margin-top: 18px;
	width: 100%;
	max-width: var(--pp-center-width);

	.peachpay-header {
		display: flex;
		flex-direction: column;
		gap: 12px;

		// padding-top: 12px;
		// border-radius: 4px;
		background-color: transparent !important;
		padding-top: 0 !important;
		border-radius: 0 !important;
		margin-bottom: 0 !important;
		border: none !important;

		& > * {
			padding: 0 24px;
		}

		.peachpay-heading {
			display: flex !important;
			justify-content: space-between;
			align-items: center;
			clear: both;
			align-self: center;
			font-weight: bolder;
			box-sizing: border-box;
			width: 100%;
			transition: all 0.3s;

			.top-nav-link {
				display: flex;
				align-items: center;
				text-decoration: none;
				color: $orchColorGray700;
				font-size: 12px;
				font-weight: 500;
				line-height: 16px;
				gap: 8px;
				border-radius: 8px;
				transition: all 0.3s;
				white-space: nowrap;

				&:hover {
					color: $orchColorLightTextPrimaryDefault !important;

					.icon {
						background-color: $orchColorLightTextPrimaryDefault;
					}
				}

				&.current {
					background-color: $orchColorLightBgBasicInteractive;
					color: $orchColorLightTextDefault;
				}

				@media screen and (max-width: 960px) {
					font-size: 11px;
				}
			}

			.left {
				display: flex;
				gap: 24px;
				align-items: center;

				& > div {
					position: relative;
				}

				.hamburger-menu {
					display: none;
				}

				.peachpay-accordion {
					margin-left: 48px;
				}

				.top-nav-link-group {
					display: flex;
					flex-direction: row;
					gap: 8px;

					.top-nav-link {
						padding: 8px 12px;

						&:first-of-type.current {
							margin-left: 12px;
						}
					}
				}
			}

			.right {
				display: flex;
				transition: all 0.3s;

				.top-nav-link {
					padding: 8px 10px;
				}
			}

			.chevron-down {
				display: none !important;
				width: 12px;
				height: 12px;
			}

			.dropdown {
				position: absolute;
				display: none;
				opacity: 0;
				top: 100%;
				margin-top: 8px;
				box-shadow: 0 6px 6px -8px rgb(0 0 0 / 30%);
				background-color: white;
				border-radius: 8px;
				flex-direction: column;
				padding: 8px 0;
				z-index: 5;
				border: 1px solid $orchColorLightBorderBasicDefault;
				white-space: nowrap;

				& > a {
					display: flex;
					padding: 12px 16px;
					text-decoration: none;
					color: $orchColorLightTextHint;
					align-items: center;
					gap: 8px;

					&:hover {
						color: $orchColorLightTextPrimaryDefault;

						.icon {
							background-color: $orchColorLightBgPrimaryDefault;
						}
					}

					&.current {
						background-color: $orchColorLightBgPrimaryDefault;
						color: white;

						.icon {
							background-color: white;
						}
					}
				}
			}
		}
	}

	#peachpay-bread-crumbs {
		display: flex;
		gap: 8px;
		margin-bottom: 8px;
		opacity: 0.8;

		.crumb {
			display: flex;
			gap: 6px;

			a {
				text-decoration: none;
				color: $orchColorGray700;
			}
		}

		div:not(.active)::after {
			content: '\203A';
			display: flex;
		}
	}

	.peachpay-logo {
		height: 34px;
		width: 150px;
		background-image: url('../../../public/img/pp-full-purple.svg');
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
	}
}

/* Side navigation */
.peachpay-side-nav {
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	width: fit-content;
	height: 100%;
	overflow-y: auto;
	z-index: 3;
	gap: 32px;
	justify-content: space-between;
	padding: 1px;
	position: sticky;
	top: 64px;
	max-height: calc(100vh - 120px);

	.nav-tab-wrapper {
		display: flex;
		flex-direction: column;
		margin: 0;
		width: 216px;
		gap: 6px;
		border-bottom: none;
		padding: 0;

		&::-webkit-scrollbar {
			height: 0 !important;
		}

		&:not(.wp-clearfix)::after {
			display: flex;
			content: unset;
		}

		& > div {
			transition: all 0.3s;
		}

		.peachpay-logo {
			display: none;
		}

		.tab-with-subtabs-container {
			display: flex;
			flex-direction: column;
		}

		.nav-tab {
			display: flex;
			width: 100%;
			justify-content: space-between;
			align-items: center;
			padding: 8px;
			margin: 0;
			box-sizing: border-box;
			font-size: 13px;
			line-height: 18px;
			font-weight: 500;
			color: $orchColorGray700;
			cursor: pointer;
			border: none;
			background: none;
			z-index: 1;
			transition: all 0.3s, font-size 0.3s ease-in-out 0s;

			&:hover {
				color: $orchColorLightTextPrimaryDefault;
				background: none;
				transition: color 0.2s;

				.chevron-down {
					background-color: $orchColorLightTextPrimaryDefault;
					opacity: 0.95;
				}

				.title > .icon:first-of-type {
					background-color: $orchColorLightTextPrimaryDefault;
				}
			}

			.title {
				display: flex;
				position: relative;
				gap: 8px;
				align-items: center;
			}

			.crown-icon {
				background-color: $orchColorLightTextWarningDefault;
			}
		}

		.nav-tab.expanded,
		.tab-with-subtabs-container:focus-within .nav-tab {
			.chevron-down {
				transform: rotate(180deg);
			}
		}

		.nav-sub-tabs {
			display: flex;
			flex-direction: column;
			margin: 8px 0 8px 32px;

			.nav-sub-tab {
				padding: 8px;
				font-size: 13px;
				line-height: 18px;
				font-weight: 500;
				color: $orchColorLightTextHint;
				text-decoration: none;
				cursor: pointer;

				&:hover {
					color: $orchColorLightTextPrimaryDefault;
					transition: color 0.2s;
				}
			}
		}

		.nav-tab:not(.has-subtabs),
		.nav-tab.current + .nav-sub-tabs > .nav-sub-tab {
			@include nav-could-be-current;
		}
	}

	.side-nav-bottom-group {
		flex-direction: column;
		gap: 12px;
		display: none;
		margin-bottom: 8px;
		padding-top: 14px;
		border-top: 1px solid #e9e9e9;

		& > * {
			align-items: center;
			display: flex;
			text-decoration: none;
			font-size: 12px;
			line-height: 16px;
			font-weight: 400;
			color: $orchColorLightTextHint;
			padding: 0 8px;
			gap: 8px;
			transition: all 0.3s;
			white-space: nowrap;

			&:hover {
				color: $orchColorLightTextPrimaryDefault !important;

				.icon {
					background-color: $orchColorLightBgPrimaryDefault;
				}
			}

			.icon {
				margin: 4px;
				width: 18px;
				height: 18px;
				background-color: $orchColorLightTextHint;
			}
		}

		.nav-tab:hover::before {
			content: '';
			background-color: #ddd;
		}
	}

	.peachpay-nav-dropdown {
		display: flex;
		flex-direction: column;
		padding: 12px 20px;
		gap: 8px;
		width: fit-content;
		height: fit-content;
		background-color: rgb(253 253 253 / 98%);
		border: 1px solid rgb(250 250 250 / 100%);
		border-radius: 12px;
		box-shadow: 2px 6px 12px -10px rgb(0 0 0 / 40%), -1px 0 12px -10px rgb(0 0 0 / 40%);

		.dropdown-tab {
			color: #616161;
			padding: 4px;
			width: fit-content;
			text-decoration: none;
			font-size: 14px;
			font-weight: 500;
			border-top: 2px solid transparent;
			border-bottom: 2px solid transparent;

			&:hover {
				color: #21105d;
				border-bottom: 2px solid #21105dcc;
			}
		}
	}
}

/* When rendered in the WC settings */
// stylelint-disable-next-line selector-class-pattern
.woocommerce_page_wc-settings {
	.peachpay-header {
		flex-direction: column-reverse !important;
		gap: 4px !important;

		& > * {
			padding: 0 !important;
		}
	}

	.pp-admin-content-wrapper {
		padding: 0 !important;

		.nav-tab-wrapper {
			border-radius: 4px !important;
		}
	}
}

/* ICONS & IMAGES */

.peachpay-logo {
	height: 34px;
	width: 150px;
	flex-shrink: 0;
	background-image: url('../../../public/img/pp-full-purple.svg');
	background-size: contain;
	background-repeat: no-repeat;
}

.peachpay {
	.icon {
		display: flex;
		width: 16px;
		height: 16px;
		background-color: $orchColorLightTextHint;
		mask-position: center center;
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		transition: all 0.3s;
	}

	.nav-tab .title {
		& > .icon:first-of-type {
			width: 22px;
			height: 22px;
		}

		& + .icon.chevron-down {
			width: 14px;
			height: 14px;
		}
	}

	.hamburger-menu {
		mask-image: url('../../../public/img/hamburger-menu.svg');
	}

	.payment-icon,
	.payment-methods-icon {
		mask-image: url('../img/payments.svg');
	}

	.field-icon {
		mask-image: url('../img/field-editor.svg');
	}

	.currency-icon {
		mask-image: url('../img/currency-switcher.svg');
	}

	.related-products-icon {
		mask-image: url('../img/product-reco.svg');
	}

	.bot-protection-icon {
		mask-image: url('../img/bot-detection.svg');
	}

	.express-checkout-icon {
		mask-image: url('../img/express-checkout.svg');
	}

	.address-autocomplete-icon {
		mask-image: url('../img/address-autocomplete.svg');
	}

	.region-icon {
		mask-image: url('../img/region.svg');
	}

	.active-status {
		position: absolute;
		width: 7px;
		height: 7px;
		border-radius: 50%;
		top: 14px;
		left: 16px;
		background-color: $orchColorGreen700;
	}

	.device-breakdown-icon {
		mask-image: url('../img/devices.svg');
	}

	.abandoned-carts-icon {
		mask-image: url('../img/cart-abandonment.svg');
	}

	.docs-icon {
		mask-image: url('../../../public/img/docs-icon.svg');
	}

	.support-icon {
		mask-image: url('../../../public/img/support-icon.svg');
	}

	.analytics-icon {
		mask-image: url('../../../public/img/analytics-icon.svg');
	}

	.settings-icon {
		mask-image: url('../../../public/img/settings-icon.svg');
	}

	.dashboard-icon {
		mask-image: url('../../../public/img/dashboard-icon.svg');
	}

	.twitter-icon {
		mask-image: url('../../../public/img/twitter.svg');
	}

	.crown-icon {
		mask-image: url('../../../public/img/crown-icon.svg');
	}

	.chevron-down {
		mask-image: url('../../../public/img/chevron-down-solid.svg');
	}

	.account-icon {
		mask-image: url('../../../public/img/account-icon.svg');
	}

	.data-icon {
		mask-image: url('../../../public/img/data-icon.svg');
	}

	.button-to-anchor {
		background: none;
		border: none;
		cursor: pointer;
	}

	.home-icon {
		mask-image: url('../../../public/img/dashboard-icon.svg');
	}

	.peachpay-heading .pp-button-continue {
		color: white !important;
		background-color: $orchColorLightBgPrimaryDefault !important;
		border-radius: 8px !important;
		font-weight: 400 !important;

		&:hover {
			color: $orchColorLightBgPrimaryDefault !important;
			background-color: $orchColorObsidian50 !important;
			box-shadow: 0 0 0 1px $orchColorLightBorderPrimaryDefault !important;

			.crown-icon {
				background-color: $orchColorLightTextWarningDefault !important;
			}
		}

		.crown-icon {
			background-color: white !important;
		}
	}

	.peachpay-side-nav .pp-button-premium {
		&:hover {
			font-weight: 600;
			font-size: 12.2px;
			transition: all 0.2s ease-in-out;

			.crown-icon {
				background-color: $orchColorLightTextWarningDefault;
				mask-size: 105% 105%;
			}
		}
	}
}

/* BREAKPOINTS */

@media screen and (max-width: 1399px) {
	.top-nav-link-group {
		& + .dropdown {
			opacity: 0;
		}

		&.expanded {
			& + .dropdown {
				opacity: 1 !important;
			}
		}
	}

	.peachpay .dropdown {
		display: flex !important;
	}

	#peachpay-nav .left {
		.peachpay-accordion {
			margin-left: 60px !important;
		}

		.top-nav-link {
			&:not(.current) {
				display: none !important;
			}

			&.current {
				.chevron-down {
					display: flex !important;
				}
			}

			&:first-of-type.current {
				margin-left: 0 !important;
			}
		}
	}
}

@media screen and (max-width: 1344px) {
	.peachpay-header > * {
		padding: 0 18px !important;
	}
}

@media screen and (max-width: 1175px) {
	.peachpay-header > * {
		width: 100%;
		padding: 0 24px;
		align-self: flex-start;
	}
}

@media screen and (max-width: 960px) {
	#peachpay-nav .left {
		gap: 12px;

		.peachpay-logo {
			background-image: url('../../../public/img/pp-short-purple.svg') !important;
			width: 28px;
			height: 28px;
			transform: none;
		}

		.peachpay-accordion {
			margin-left: 0 !important;
		}
	}
}

@media screen and (max-width: 782px) {
	#peachpay-nav .left {
		width: 100%;
		justify-content: space-between;

		.hamburger-menu {
			display: flex !important;
			cursor: pointer;
			width: 18px;
			height: 18px;
		}

		.peachpay-logo {
			display: none !important;
		}

		.peachpay-accordion {
			margin-right: -12px;
		}

		.top-nav-link.current {
			background-color: transparent !important;
		}

		.dropdown {
			right: 0;
		}
	}

	#peachpay-nav .right {
		.top-nav-link {
			display: none !important;
		}
	}

	.peachpay-side-nav {
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		z-index: 100000;
		overflow-y: auto;
		overflow-x: hidden;
		background-color: white;
		padding: 16px;
		box-shadow: 2px 0 16px -10px rgb(0 0 0 / 20%), -2px 2px 8px -6px rgb(0 0 0 / 20%);
		transition: all 0.24s ease;
		max-height: none;

		&:not(.shown, :focus-within) {
			left: -100%;
		}

		&.shown,
		&:focus-within {
			left: 0;
		}

		.peachpay-logo {
			display: flex !important;
			margin: 8px 0;
		}

		.side-nav-bottom-group {
			display: flex;
		}
	}
}
