@import '../../@shared/scss/variables';

.peachpay .payment-provider-header {
	gap: 26px !important;

	&.is-connected {
		border-bottom: 1px solid rgb(0 0 0 / 18.3%);
		padding-bottom: 24px;
	}

	& > div:first-of-type {
		display: flex;
		gap: 16px;
		justify-content: space-between;

		@media screen and (max-width: 1080px) {
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-end;
			gap: 32px;
		}

		@media screen and (max-width: 782px) {
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-start;
			gap: 16px;
		}

		@media screen and (max-width: 620px) {
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			gap: 32px;
		}
	}

	.provider-title {
		img {
			height: 32px;
		}

		font-size: 18px;
		font-weight: 500;
	}

	.buttons-container {
		& > [class*='button'] {
			min-width: 200px;
			text-align: center;
		}

		@media screen and (max-width: 1080px) {
			&,
			& > [class*='button'] {
				width: 100%;
			}
		}

		@media screen and (max-width: 782px) {
			width: 200px;
		}

		@media screen and (max-width: 620px) {
			width: 100%;
		}
	}

	.connected-info {
		.connected-success {
			font-size: 16px;
		}
	}

	.account-info,
	.account-info a {
		color: $orchColorLightTextHint;
		font-size: 13px;
		line-height: 21px;
		word-break: break-all;

		& a:hover {
			color: $orchColorLightTextDefault;
		}
	}

	.provider-description {
		gap: 18px;

		& > * {
			color: $orchColorLightTextHint;
			font-size: 14px !important;
			line-height: 24px;

			@media screen and (max-width: 782px) {
				font-size: 13px;
				line-height: 21px;
			}
		}
	}

	.provider-info {
		display: flex;
		gap: 6px;
		color: $orchColorLightTextHint;
		font-size: 14px !important;
		line-height: 24px;

		&:has(a:hover) {
			.info-icon {
				background-color: $orchColorLightTextDefault;
			}
		}

		@media screen and (max-width: 782px) {
			font-size: 13px;
			line-height: 21px;
			gap: 4px;
		}

		a {
			color: $orchColorLightTextPrimaryDefault;

			&:hover {
				color: $orchColorLightTextPrimaryInteractive;
			}
		}

		.info-icon {
			background-color: $orchColorLightTextHint;
			margin: 3px 0;
			flex-shrink: 0;

			@media screen and (max-width: 782px) {
				width: 17px;
				height: 17px;
			}
		}
	}

	.method-icons {
		display: flex;
		gap: 8px 10px;
		flex-wrap: wrap;

		img {
			width: 24px !important;
			height: 24px !important;
		}

		span {
			display: flex;
		}

		.gateway-title {
			white-space: nowrap;
			color: $orchColorLightTextHint;
			font-size: 14px;
			font-weight: 500;
			line-height: 24px;
		}

		& > span {
			align-items: center;
			gap: 6px;

			&:last-of-type {
				.gateway-title span:last-of-type {
					display: none;
				}
			}
		}
	}
}
