#peachpay-gateway-settings,
.peachpay {
	.settings-container {
		background-color: white;
		padding: 0.2rem 1rem 1rem;
		margin-bottom: 0.5rem;
		border-radius: 0.3rem;
		border: 1px solid rgb(0 0 0 / 18.3%);

		hr {
			margin: 0;
		}

		p {
			margin: 0.5rem 0;
		}
	}

	.advanced-gateway-settings {
		padding: 16px 24px;

		tbody {
			margin: 0 !important;
		}

		.gateway-list {
			border-top: 1px solid rgb(0 0 0 / 18.3%);
		}
	}

	.action-needed {
		border-left: 7px solid red;
		padding-left: calc(1rem - 7px);
	}
}

#peachpay-gateway-settings {
	// the default WC style for number inputs cause the content to not fit in smaller screens.
	input[type='number'] {
		width: revert !important;
	}

	input[type='text'],
	select {
		max-width: 400px;
		width: 100% !important;
	}
}
