#pp-premium-modal {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgba(0 0 0 / 40%);

	// Wordpress sidebar is 9990
	z-index: 100000;

	.premium-modal-content {
		position: relative;
		display: flex;
		flex-direction: column;
		background-color: #fefefe;
		margin: 10% auto 0;
		padding: 20px;
		border-radius: 12px;
		max-width: 450px;

		.premium-modal-close {
			position: absolute;
			top: 16px;
			right: 26px;
			color: #aaa;
			font-size: 28px;
			font-weight: bold;

			&:hover,
			&:focus {
				color: black;
				text-decoration: none;
				cursor: pointer;
			}
		}

		.premium-modal-header {
			display: flex;
			align-items: center;
			margin: 16px;
			font-weight: bold;
			font-size: 18px;
			line-height: 28px;
			font-family: Inter, sans-serif;
			font-style: normal;
			font-feature-settings: salt on, liga off;

			svg {
				margin: 0 4px;
			}
		}

		.feature-list {
			width: 90%;
			text-align: start;
			margin-left: 16px;
			margin-bottom: 8px;

			.feature-element {
				display: flex;
				align-items: center;
				gap: 4px;
				font-weight: 500;
				font-size: 14px;
				line-height: 20px;
				font-family: Inter, sans-serif;
				font-style: normal;
				font-feature-settings: salt on, liga off;

				&:first-child {
					margin-top: 0;
				}
			}
		}

		.premium-modal-actions {
			display: flex;
			gap: 8px;
			margin: 0 auto;
			text-align: center;

			.premium-modal-actions-text {
				margin: 8px;
				font-weight: normal;
				font-size: 16px;
				line-height: 26px;
				font-family: Inter, sans-serif;
				font-style: normal;
				font-feature-settings: salt on, liga off;
			}

			.custom-button-2 {
				border: 1px solid #21105d;
				color: #21105d;
				background-color: white;
			}

			.custom-button-2:disabled {
				border: 1px solid #bdbdbd;
				background: rgba(158 158 158 / 16%);
				color: #bdbdbd;
			}

			.custom-button-2:hover {
				cursor: pointer;
				background-color: #21105d22;
			}
		}

		.premium-modal-footer {
			margin-left: auto;
			margin-right: auto;
			font-weight: 200;
			font-size: 14px;
			line-height: 20px;
			font-family: Inter, sans-serif;
			font-style: normal;
			font-feature-settings: salt on, liga off;
		}
	}
}

#peachpay-premium-trial-notice.notice,
#peachpay-premium-locked-banner.notice,
#peachpay-premium-trial-ending-notice.notice {
	display: flex;
	margin: 0;
}
