.peachpay {
	.gateway-provider-info {
		display: flex;
		text-align: center;

		p {
			margin: 0;
			width: 100%;
		}
	}

	// This section is only for resetting styles.
	.gateway {
		display: flex;
		width: 100%;
		justify-content: space-between;
		gap: 16px;
		padding: 24px 0 8px;
		position: relative;

		&:not(:first-of-type) {
			border-top: 1px solid rgb(0 0 0 / 18.3%);
		}

		@media only screen and (max-width: 1080px) {
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-end;
			gap: 32px;

			.buttons-container,
			.buttons-container > * {
				width: 100% !important;
			}
		}

		@media only screen and (max-width: 782px) {
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-start;
			gap: 16px;

			.buttons-container {
				width: 200px !important;
			}
		}

		@media only screen and (max-width: 620px) {
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			gap: 32px;

			.buttons-container {
				width: 100% !important;
			}
		}

		.info {
			min-width: 300px;
		}

		h4 {
			margin: unset;
			font-size: 18px;
			font-weight: 500;
		}

		p {
			margin: unset;
		}

		.list-item-heading {
			height: 36px;
			display: flex;
			align-items: center;

			& > span {
				margin-left: 0 !important;
				height: 100%;

				img {
					height: 100%;
					object-fit: contain;
					max-height: 36px !important;
				}
			}
		}

		.description {
			display: flex;
			width: 100%;

			& > span {
				padding-bottom: 16px;
			}

			.details {
				width: 100%;
				display: flex;
				gap: 20px;
				padding-bottom: 4px;
				justify-content: flex-start;
				flex-wrap: wrap;

				@media screen and (max-width: 782px) {
					gap: 20px 16px;
				}

				& > div {
					display: flex;
					max-width: max(32%, 250px);
					position: relative;
					flex-grow: 1;

					@media screen and (max-width: 1420px) {
						max-width: max(48%, 200px);
					}

					@media screen and (max-width: 1230px) {
						max-width: max(45%, 160px);
					}

					h4 {
						display: flex;
						font-size: 12px;
						width: max-content;
						color: #616161;
						font-weight: 500;
					}

					div {
						display: flex;

						p {
							color: #212121;
							font-size: 11px;

							&::-webkit-scrollbar {
								display: none;
							}
						}
					}
				}
			}
		}

		.general-status {
			margin-left: 4px;

			&.active .inactive {
				display: none;
			}

			&.inactive .active {
				display: none;
			}

			.active,
			.inactive {
				display: flex;
				padding: 2px 12px;
				border-radius: 8px;
				font-size: 12px;
				line-height: 14px;
			}

			.active {
				color: white;
				border: 1px solid #2e7d32;
				background-color: #2e7d32;
			}

			.inactive {
				color: $orchColorLightTextBasicDefault;
				border: 1px solid $orchColorLightTextBasicDefault;
				background-color: #eee;
			}
		}

		.location-status {
			display: flex;
			column-gap: 16px;

			.location {
				display: flex;
				align-items: center;
				font-size: 12px;
				font-weight: 600;
				line-height: 14px;
				width: fit-content;
				color: $orchColorLightTextDisabled;

				&::before {
					content: ' ';
					display: inline-block;
					border: 2px solid $orchColorLightTextDisabled;
					width: 7px;
					height: 7px;
					border-radius: 100px;
					margin-right: 4px;
				}

				&.active {
					color: #212121;

					&::before {
						background-color: #2e7d32;
						border: 2px solid #2e7d32;
					}
				}
			}
		}

		span.peachpay-gateway-icons {
			display: flex;
			width: 5rem !important;
			height: 3rem !important;
			padding: 0.2rem 0;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			border-radius: 0.4rem;
			border: 0.5px solid #dedede;
			box-shadow: 0 1px 6px -2px #eee;

			img {
				display: flex;
				justify-self: center;
				align-self: center;
				margin: 0;
				padding: 0;

				&:nth-child(2n) {
					flex-basis: 100% !important;
				}

				&:only-child {
					height: 1.5rem;
				}

				&:not(:only-child) {
					display: flex;
					height: 1.2rem;
					width: 2rem;
				}
			}
		}

		.buttons-container {
			display: flex;
			flex-direction: column;
			gap: 12px;

			.setup-button,
			.manage-button,
			.activate-button,
			.deactivate-button {
				min-width: 200px;
				width: max-content;
				white-space: nowrap;
			}

			&.needs-setup {
				.manage-button,
				.activate-button,
				.deactivate-button {
					display: none;
				}
			}

			&.enabled {
				.setup-button,
				.activate-button {
					display: none;
				}

				.manage-button {
					&:hover {
						.arrow-top-right {
							background-color: white;
						}
					}
				}
			}

			&.disabled {
				.setup-button,
				.deactivate-button {
					display: none;
				}

				.manage-button:hover {
					.arrow-top-right {
						background-color: #21105d;
					}
				}
			}

			.arrow-top-right {
				width: 12px;
				height: 12px;
				transition: all 0.3s;
			}

			.setup-button .arrow-top-right {
				background-color: #21105d;
			}

			.manage-button .arrow-top-right {
				background-color: #424242;
			}

			.button-secondary {
				height: 20px;
			}

			.spinner {
				background-size: contain;
				visibility: visible;
				display: none;
				width: 16px;
				height: 16px;
				margin: 0;
			}

			.activate-button,
			.deactivate-button {
				&.loading {
					.spinner {
						visibility: visible;
						display: flex;
					}
				}
			}
		}

		.tooltip-body {
			text-align: left;
			padding: 1rem;
			display: flex;
			flex-direction: column;
			gap: 8px;

			h4 {
				padding: 0;
				margin: 0;
				font-size: 13px;
				font-weight: 500;
			}

			p {
				font-size: 12px;
				color: rgb(226 226 226);
				padding: 0;
				margin: 0.1rem 0 0 1rem;
			}
		}

		.gateway-enabled-locations {
			.enabled-locations {
				display: flex;
				gap: 12px;

				.enabled-location {
					display: flex;
					text-anchor: middle;
					align-items: center;
					gap: 4px;

					&::before {
						content: '';
						display: flex;
						width: 8px;
						height: 8px;
						margin-top: 1px;
						color: $orchColorLightTextHint;
						box-sizing: border-box;
						border-radius: 50%;
					}

					&[active] {
						&::before {
							background-color: $orchColorLightBgSuccessDefault;
						}
					}

					&[inactive] {
						opacity: 0.8;

						&::before {
							background-color: $orchColorLightTextHint;
						}
					}
				}
			}

			div {
				display: flex;
			}
		}

		@media screen and (max-width: 1344px) {
			.col-9 > div {
				flex-direction: column;
				align-items: flex-start;
				gap: 4px;

				& > span {
					display: none;
				}
			}
		}
	}
}
