.peachpay {
	.indicate-unsaved-change:not(tbody) {
		box-shadow: 0 0 4px 0 $orchColorLightBorderInfoInteractive !important;
	}

	#field-table tbody.indicate-unsaved-change {
		@for $i from 0 through 30 {
			tr:nth-child(#{$i + 1}):not([data_originalValue="#{$i}"]) {
				.sort i::before {
					filter: drop-shadow(0 0 6px $orchColorLightBorderInfoInteractive) !important;
				}
			}
		}
	}

	div:focus,
	button:focus,
	:focus-visible,
	input:focus + .pp-slider {
		box-shadow: 0 0 0 1px #4f94d4, 0 0 2px 1px rgb(79 148 212 / 80%);
		outline: 1px solid transparent;
		transition: box-shadow, outline 0.2s;
	}
}
