#wpbody-content {
	padding-bottom: 0;
}

.peachpay-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 24px;
	box-sizing: border-box;

	& * {
		box-sizing: border-box;
	}

	div:focus,
	:focus-visible,
	input:focus + .pp-slider {
		box-shadow: 0 0 0 1px #4f94d4, 0 0 2px 1px rgb(79 148 212 / 80%);
		outline: 1px solid transparent;
		transition: box-shadow, outline 0.2s;
	}
}

.pp-admin-content-wrapper {
	display: flex;
	gap: 16px;
	flex-direction: row;
	justify-content: space-between;
	width: 100%;
	max-width: var(--pp-center-width);
	padding: 0 24px;
	margin-bottom: 20px;
	transition: all 0.3s;

	@media screen and (max-width: 1344px) {
		padding: 0 18px;
	}

	@media screen and (max-width: 782px) {
		gap: 18px;
	}

	.pp-admin-content {
		display: flex;
		flex-direction: column;
		width: 100%;
		gap: 24px;
		overflow: auto;
	}
}

// stylelint-disable-next-line selector-id-pattern
#peachpay_settings_container {
	position: relative;
	display: flex;
	flex-direction: column;
	background-color: white;
	border-radius: 8px;
	width: 100%;
	box-sizing: border-box;
}

// SEE-MORE BUTTONS
.peachpay {
	.see-more-wrap {
		position: relative;

		.see-more-state {
			display: none;
		}

		.see-more-trigger.hide + .see-more-target .fade-bottom {
			display: none;
		}

		// &:has(.see-more-trigger.hide) {
		// 	.fade-bottom {
		// 		display: none;
		// 	}
		// }

		.see-more-target {
			overflow: hidden;
			position: relative;
		}

		.fade-bottom {
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 16px;
			background-image: linear-gradient(0deg, #fff, rgb(255 255 255 / 0%));
		}

		.see-more-trigger {
			cursor: pointer;
			display: flex;
			color: #666;
			font-size: 0.9em;

			&::before {
				content: 'See more';
				text-decoration: underline;
				color: #0073aa;
			}
		}
	}

	.see-more-state:checked + div {
		.see-more-target {
			max-height: 999em !important;
		}

		.fade-bottom {
			background: transparent;
		}

		.see-more-trigger::before {
			content: 'See less';
			text-decoration: underline;
			color: #0073aa;
		}
	}
}

// OTHER PLUGINS' NOTICES
// stylelint-disable-next-line selector-class-pattern
[class*='peachpay'] #wpbody-content > :not(
.peachpay,
.pp-test-notice-wrapper,
#screen-meta,
.notice[id*='peachpay_message'],
#pp-onboarding-tour-modal,
style,
script
) {
	width: 98%;
	box-sizing: border-box;
	display: flow-root;
	margin: 8px auto;
	max-width: var(--pp-center-width);
	float: none;
}

// stylelint-disable-next-line selector-id-pattern
#toplevel_page_peachpay div > img {
	padding: 7px 0 0 !important;
}
