.pp-test-notice-wrapper {
	position: sticky;
	top: 32px;
	transform: translateY(-1px);
	width: 100%;
	height: 0;
	z-index: 10;
	margin-bottom: 24px;

	@media screen and (max-width: 782px) {
		top: 46px;
	}

	@media screen and (max-width: 600px) {
		top: 0;
	}
}

.pp-test-notice {
	width: 100%;
	font-size: 13px;
	line-height: 24px;
	font-weight: 600;
	background-color: $orchColorOrange800;
	color: white;
	text-align: center;
}

.pp-sub-nav-container {
	margin: 24px 24px 0;

	.pp-sub-nav-button {
		color: #757575;
		background-color: #eee;
		font-size: 14px;
		font-weight: 500;
		padding: 8px 20px;
		border-radius: 12px;
		cursor: pointer;
		transition: all 0.3s;

		&:hover {
			filter: brightness(80%);
		}

		@media screen and (max-width: 720px) {
			font-size: 12px;
			padding: 6px 18px;
		}
	}
}

.pp-section-nav-container {
	padding: 0;
	gap: 0 8px;
	position: relative;
	display: flex;
	justify-content: flex-start;
	border-bottom: 1px solid #c4c4c4;
	overflow-x: auto;
	flex-wrap: wrap;

	&::-webkit-scrollbar {
		height: 0 !important;
	}

	@media screen and (max-width: 1175px) {
		gap: 0 4px;
	}

	a {
		display: flex;
		margin: 0;
		padding: 8px 16px;
		justify-content: center;
		line-height: 24px;
		font-weight: 500;
		font-size: 13px;
		text-decoration: none;
		color: #616161;
		position: relative;
		z-index: 0;
		width: max-content;
		white-space: nowrap;
		border-top: 4px solid transparent;
		border-bottom: 4px solid transparent;
		cursor: pointer;
		transition: all 0.3s;

		&::before {
			content: '';
			position: absolute;
			top: 6px;
			right: 4px;
			bottom: 6px;
			left: 4px;
			display: flex;
			border-radius: 4px;
			z-index: -1;
			background-color: transparent;
			transition: all 0.3s;
		}

		&:hover {
			color: var(--pp-color);

			&::before {
				background-color: #ddd;
			}
		}

		&.pp-sub-nav-link-active {
			color: var(--pp-color);
			border-bottom: 4px solid var(--pp-color);
		}

		@media screen and (max-width: 1175px) {
			padding: 6px 10px;
			font-size: 12.5px;
		}

		@media screen and (max-width: 1024px) {
			padding: 4px 10px;
		}
	}
}

.pp-header {
	display: flex;
	width: 100%;
	flex-direction: column;
	box-sizing: border-box;
	border-bottom: 1px solid #c4c4c4;
	gap: 18px;
	padding: 24px;
	transition: all 0.3s;

	&.no-border-bottom {
		border-bottom: none;
	}

	&.pp-sub-nav-peachpay {
		gap: 8px;

		th {
			font-size: 16px;

			@media screen and (max-width: 1024px) {
				font-size: 14px;
			}
		}
	}

	@media screen and (max-width: 1024px) {
		padding: 18px 14px;
	}

	@media screen and (max-width: 782px) {
		flex-direction: column;
	}

	th {
		width: 100%;
		padding: 0;
		font-size: 18px;
		font-weight: 700;
		line-height: 27px;
		display: flex;
		flex-shrink: 0;

		@media screen and (max-width: 1024px) {
			font-size: 16px;
		}
	}

	td {
		display: flex;
		width: 100%;
		padding: 0;
		margin: 0;
	}
}

/* Same-page navigation */

// stylelint-disable-next-line selector-id-pattern
.pp-admin-content-wrapper.has-hashed-tabs #peachpay_settings_container {
	opacity: 0;
}

$hashes: 'stripe', 'square', 'paypal', 'poynt', 'authnet', 'peachpay', 'full', 'floating', 'pr', 'ocu';

// We need to loop through a predefined list to be able to use one of these applicable ones as a variable
@each $hash in $hashes {
	.pp-admin-content-wrapper.section-#{$hash} #peachpay_settings_container {
		opacity: 1 !important;
	}

	.pp-admin-content-wrapper.section-#{$hash} {
		.nav-sub-tab.hashed {
			&[href$="##{$hash}"] {
				@include nav-is-current;
			}
		}

		// Set active style to the ~section~ buttons (level 1 same-page navigation)
		.pp-section-nav-container {
			.pp-sub-nav-button {
				&[href="##{$hash}"] {
					color: $orchColorLightTextPrimaryDefault;
					border-bottom: 4px solid $orchColorLightBorderPrimaryDefault;
				}
			}
		}

		// Set active style to the ~sub-section~ buttons (level 2 same-page navigation)
		.pp-sub-nav-container {
			.pp-sub-nav-button {
				&[href="##{$hash}"] {
					color: $orchColorLightBgPaperExtralight;
					background-color: $orchColorLightBgPrimaryDefault;
				}
			}
		}

		// Hide sections that shouldn't show on the page with the current hash
		.pp-header {
			&:not(.pp-sub-nav-#{$hash}) {
				display: none;
			}
		}
	}
}

// stylelint-disable-next-line selector-class-pattern
.peachpay_premium_locked_settings .pp-section-nav-container {
	z-index: 2;
	background-color: white;
	border-radius: 4px 4px 0 0;
}

.peachpay-settings-section {
	.currency-widget-section {
		display: flex;
		align-items: center;
		gap: 12px;
		margin-top: 12px;
	}
}

/* within payment settings */
// TODO is this a dead rule?
[name='option_page'][value='peachpay_payment'] ~ .form-table {
	tbody {
		margin: 0 !important;
	}
}
