.accordion-tab {
	position: relative;

	&:not(.expanded) {
		& + .accordion-content:not(:focus-within) {
			margin-top: 0 !important;
			margin-bottom: 0 !important;
			padding-top: 0 !important;
			padding-bottom: 0 !important;
			gap: 0 !important;
			pointer-events: none;
			max-height: fit-content;
			
			& > a,
			& > div {
				transform: rotateX(90deg) scaleY(0);
				padding-top: 0 !important;
				padding-bottom: 0 !important;
				opacity: 0;
				max-height: 0;

				::before {
					background-color: transparent;
				}

				@for $i from 1 through 10 {
					&:nth-last-child(#{$i}) {
						transition: all .2s ease ($i * 0.025s), opacity .3s ease ($i * 0.016s);
					}
				}
			}
		}
	}

	&.expanded + .accordion-content,
	& + .accordion-content:focus-within {
		/* Needed trick for transition animation; this is set to a value that the component will likely never reach. */
		max-height: 700px;
		gap: 4px;
		opacity: 1 !important;

		& > a,
		& > div {
			opacity: 1;
			/* For transition animation */
			max-height: 100px;

			@for $i from 1 through 10 {
				&:nth-child(#{$i}) {
					transition: all (.25s + $i * 0.03s) ease ($i * 0.025s), opacity .3s ease ($i * 0.06s);
				}
			}
		}
	}

	& + .accordion-content {
		transition: all 0.3s, max-height .17s;
		transition-timing-function: ease-in-out;

		& > a,
		& > div {
			transform: rotateX(0deg) scaleY(1);
			transform-origin: top left;
			transition: font-size .3s ease-in-out 0s;
		}
	}
}