/**
 * CSS to create a Modal window. Uses the :active pseudo selector to determine if
 * the modal should be open based on the URL hash.
 *
 * @url https://codepen.io/timothylong/pen/AJxrPR
 * @example
```html
<a class="btn" href="#open-modal">Basic CSS-Only Modal</a>
<div id="open-modal" class="modal-window">
    <div>
        <a href="#" title="Close" class="modal-close">Close</a>
        <p>Modal Content goes here</p>
    </div>
</div>
```
 */
.peachpay {
	.modal-window {
		position: fixed;
		background-color: rgb(79 79 79 / 47.4%);
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 999;
		visibility: hidden;
		opacity: 0;
		pointer-events: none;
		transition: all 0.3s;

		.outside-close {
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 1000;
		}

		&:target,
		&.open {
			visibility: visible;
			opacity: 1;
			pointer-events: auto;
		}

		& > div {
			display: flex;
			flex-direction: column;
			z-index: 1001;
			width: 450px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			padding: 1.5em;
			background: white;
			border-radius: 0.3rem;

			h2 {
				color: black !important;
				margin: 0 !important;
				padding: 0 !important;
				text-shadow: none !important;
				font-weight: 700 !important;
			}

			h3 {
				color: black !important;
				margin: 0 !important;
				padding: 0 !important;
				font-size: 1rem !important;
			}
		}
	}

	.modal-close {
		color: rgb(139 139 139);
		line-height: 60px;
		position: absolute;
		right: 0;
		text-align: center;
		top: 0;
		width: 70px;
		text-decoration: none;

		&:hover {
			color: black;
		}
	}
}
