// Used with navigation tabs that are highlighted when 'current'.
@mixin nav-could-be-current {
	position: relative;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		z-index: -1;
		box-sizing: border-box;
		display: flex;
		width: 100%;
		border-radius: 4px;
		background-color: transparent;
		transition: all 0.3s !important;
	}

	&.current {
		@include nav-is-current;
	}
}

// Used to style the current navigation tab.
@mixin nav-is-current {
	color: $orchColorLightTextPrimaryDefault;

	&::before {
		content: '';
		background-color: $orchColorObsidian100 !important;
	}

	.title > .icon:first-of-type {
		background-color: $orchColorLightTextPrimaryDefault;
	}
}
