.dashboard-container {
	display: flex;
	flex-direction: column;
	align-items: center;

	.dashboard {
		display: flex;
		width: 100%;
		align-self: center;
		flex-direction: column;
		gap: 40px;
		font-feature-settings: 'salt' on, 'liga' off;
	}

	.dashboard-section-content {
		display: grid;
		gap: 12px;
	}

	.dashboard-section-add-ons .dashboard-section-content,
	.dashboard-section-analytics .dashboard-section-content,
	.dashboard-section-payment-methods .dashboard-section-content {
		grid-template-columns: repeat(3, calc(calc(100% - 24px) / 3));
	}

	@media screen and (max-width: 1240px) {
		.dashboard-section-analytics .dashboard-section-content {
			grid-template-columns: repeat(2, calc(calc(100% - 12px) / 2));
		}
	}

	@media screen and (max-width: 1140px) {
		.dashboard-section-payment-methods .dashboard-section-content,
		.dashboard-section-add-ons .dashboard-section-content {
			grid-template-columns: repeat(2, calc(calc(100% - 12px) / 2));
		}
	}

	@media screen and (max-width: 650px) {
		.dashboard-section-payment-methods .dashboard-section-content,
		.dashboard-section-add-ons .dashboard-section-content {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	.dashboard-section-card {
		display: flex;
		flex-direction: column;
		background-color: white;
		border-radius: 8px;
		transition: all 0.3s;

		@media screen and (max-width: 1240px) {
			gap: 10px;
		}

		.title-button-row {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	.dashboard-section-analytics {
		.dashboard-section-card {
			padding: 12px;
			gap: 14px;
			align-items: center;
			justify-content: space-between;

			&:first-of-type {
				justify-content: space-between;
			}

			@media screen and (max-width: 1240px) {
				gap: 6px;
			}

			.dashboard-section-analytics-array-data-container {
				display: flex;
				width: 100%;
				justify-content: center;
				transition: all 0.3s;

				& > div {
					position: relative;
					padding: 4px 12px;
					transition: all 0.3s;

					&:only-of-type {
						.dashboard-section-analytics-array-data {
							font-size: 18px;
						}
					}

					&:not(:last-of-type)::before {
						content: '';
						position: absolute;
						width: 1px;
						top: 15%;
						right: 0;
						height: 75%;
						background-color: rgb(0 0 0 / 20%);
					}
				}

				.dashboard-section-analytics-array-data-label {
					display: flex;
					font-size: 12px;
					line-height: 16px;
					font-weight: 500;
					color: #828282;
					transition: all 0.3s;
				}

				.dashboard-section-analytics-single-data {
					line-height: 48px;
					padding-left: 20px;
					padding-right: 20px;
					position: relative;
					transition: all 0.3s;
				}

				.dashboard-section-analytics-array-data {
					display: flex;
					font-size: 16px;
					line-height: 28px;
					color: #424242;
					transition: all 0.3s;
				}

				@media screen and (max-width: 782px) {
					& > div {
						&:not(:only-of-type) {
							.dashboard-section-analytics-array-data-label {
								font-size: 11px;
								line-height: 14px;
							}

							.dashboard-section-analytics-array-data {
								font-size: 15px;
								line-height: 24px;
							}
						}
					}
				}

				@media screen and (max-width: 720px) {
					& > div {
						padding: 4px 12px;

						&:not(:only-of-type) .dashboard-section-analytics-array-data {
							font-size: 14px;
							line-height: 22px;
						}
					}
				}

				@media screen and (max-width: 650px) {
					& > div {
						padding: 4px 10px;
					}
				}

				&.column-view {
					flex-direction: column;
					width: 100%;

					& > div {
						&:not(:only-of-type) {
							flex-direction: row;
							justify-content: space-between;

							&::before {
								top: revert;
								right: revert;
								bottom: 0;
								left: 2%;
								width: 96%;
								height: 1px;
								background-color: rgb(0 0 0 / 10%);
							}

							.dashboard-section-analytics-array-data-label {
								font-size: 14px;
							}

							.dashboard-section-analytics-array-data {
								font-size: 16px;
								line-height: 24px;
							}

							.dashboard-section-analytics-single-data {
								line-height: 24px;
							}
						}
					}
				}
			}

			.dashboard-section-analytics-description {
				display: flex;
				font-size: 14px;
				line-height: 21px;
				width: max-content;
				color: #616161;
			}

			@media screen and (max-width: 1240px) {
				.dashboard-section-analytics-description {
					font-size: 12px;
				}
			}
		}
	}

	.dashboard-section-payment-methods {
		.dashboard-section-card {
			padding: 24px;
			gap: 12px;
		}
	}

	.dashboard-section-add-ons {
		.dashboard-section-card {
			padding: 24px;
			gap: 12px;
		}
	}

	.dashboard-section-title {
		font-size: 20px;
		font-weight: 500;
		line-height: 24px;
		color: #4a4a4a;
		padding: 0 2px;
	}

	@media screen and (max-width: 1240px) {
		.dashboard-section-title {
			font-size: 18px;
		}
	}

	.dashboard-section-card-title {
		font-size: 18px;
		font-weight: 700;
		line-height: 27px;
	}

	@media screen and (max-width: 1240px) {
		.dashboard-section-card-title {
			font-size: 16px;
		}
	}

	.dashboard-section-analytics .dashboard-section-card-title {
		font-size: 16px;
	}

	@media screen and (max-width: 1240px) {
		.dashboard-section-analytics .dashboard-section-card-title {
			font-size: 14px;
		}
	}

	.dashboard-card-button {
		display: flex;
		align-items: center;
		gap: 4px;
		text-decoration: none;
		position: relative;
		transition: all 0.3s;
	}

	.dashboard-card-button:not([enabled]) {
		padding: 8px 8px 8px 12px;
		border: 1px solid transparent !important;
	}

	.dashboard-card-button[enabled] {
		padding: 8px;
	}

	@media screen and (max-width: 1240px) {
		.dashboard-card-button:not([enabled]) {
			padding: 6px 6px 6px 10px;
		}
	}

	.dashboard-section-card:hover .dashboard-card-button:not([enabled]) {
		color: white;
		background-color: #21105d;
	}

	.dashboard-card-button div {
		display: flex;
		transition: all 0.3s;
	}

	.dashboard-card-button div:nth-of-type(1) {
		margin: 3.33px 3.33px 3.33px 0;
		width: 9.33px;
		height: 9.33px;
		opacity: 1;
		background-color: #757575;
		mask-image: url('../../../public/img/plus-sign.svg');
		mask-position: center;
	}

	.dashboard-card-button[enabled] div:nth-of-type(1),
	.dashboard-section-card:hover .dashboard-card-button:not([enabled]) div:nth-of-type(1) {
		margin: 0;
		width: 0;
		height: 0;
		opacity: 0;
	}

	.dashboard-section-add-ons .dashboard-section-card:hover .dashboard-card-button:not([enabled]) div:nth-of-type(1) {
		background-color: white;
	}

	.dashboard-card-button:not([enabled]) div:nth-of-type(3) {
		display: flex;
		margin: 0;
		width: 0;
		height: 0;
		opacity: 0;
		background-color: white;
		mask-image: url('../../../core/admin/assets/img/chevron-right.svg');
		mask-position: center;
		transition: all 0.3s;
	}

	.dashboard-section-card:hover .dashboard-card-button:not([enabled]) div:nth-of-type(3) {
		margin: 3.33px 0 3.33px 3.33px;
		width: 9.33px;
		height: 9.33px;
		opacity: 1;
	}

	.pp-homepage-view-more {
		font-weight: 500;
		font-size: 12px;
		color: #21105d;
		text-decoration: none;
		cursor: pointer;
		transition: all 0.1s;
	}

	.pp-homepage-view-more:hover {
		color: #21105d;
		filter: brightness(150%);
		transform: scale(1.01);
		transform-origin: right;
	}

	.dashboard-section-analytics-data {
		display: flex;
		flex-shrink: 1;
		font-size: 24px;
		font-weight: 500;
		line-height: 36px;
		width: 100%;
		justify-content: center;
	}

	.image-container {
		height: 96px;
		width: fit-content;
		overflow: visible;
		transition: all 0.2s;
	}

	.dashboard-section-add-ons .dashboard-section-card:not(:hover) .image-container {
		border: 0.3px solid white;
	}

	.dashboard-section-add-ons .dashboard-section-card:hover .image-container {
		border: 0.3px solid #424242;
	}

	.dashboard-section-payment-methods .dashboard-section-card:hover .image-container img {
		filter: drop-shadow(0 0 2.8px rgb(150 150 150 / 15%));
	}

	.dashboard-section-payment-methods .dashboard-section-card:nth-of-type(1):hover .image-container img,
	.dashboard-section-payment-methods .dashboard-section-card:nth-of-type(3):hover .image-container img {
		filter: drop-shadow(0 0 2.6px rgb(150 150 150 / 60%));
	}

	.dashboard-section-add-ons .image-container {
		padding: 12px;
		background-color: #f7f4ff;
		border-radius: 4px;
		width: 96px;
	}

	.dashboard-section-payment-methods .image-container {
		height: 64px;
	}

	@media screen and (max-width: 1240px) {
		.dashboard-section-payment-methods .image-container {
			height: 56px;
		}
	}

	.image-container img {
		object-fit: contain;
		height: 100%;
		transition: all 0.4s;
	}

	.dashboard-section-add-ons .image-container img {
		width: 100%;
	}

	.dashboard-section-card-description {
		font-size: 16px;
		line-height: 24px;
		color: #616161;
	}

	@media screen and (max-width: 1240px) {
		.dashboard-section-card-description {
			font-size: 14px;
			line-height: 20px;
		}
	}

	@media screen and (max-width: 960px) {
		.dashboard-section-card-description {
			font-size: 12px;
			line-height: 20px;
		}
	}

	.sub-logos {
		display: flex;
		align-items: center;
		gap: 12px;
		flex-wrap: wrap;
		transition: all 0.3s;
	}

	@media screen and (max-width: 1024px) {
		.sub-logos {
			gap: 4px;
		}
	}

	.sub-logos img {
		width: 53px;
		height: 32px;
		display: flex;
		transition: all 0.3s;
	}

	@media screen and (max-width: 1024px) {
		.sub-logos img {
			width: 40px;
			height: 24px;
		}
	}

	.sub-logos-toggle {
		display: flex;
		width: fit-content;
		padding: 8px 6px;
		margin: 0 2px;
		font-size: 12px;
		line-height: 4px;
		font-weight: 600;
		color: #9e9e9e;
		border: 1px solid #9e9e9e;
		box-sizing: border-box;
		border-radius: 4px;
		text-anchor: middle;
		vertical-align: middle;
		cursor: pointer;
		transition: all 0.3s;
	}

	@media screen and (max-width: 1024px) {
		.sub-logos-toggle {
			font-size: 11px;
			padding: 6px 4px;
			margin: 2px;
		}
	}

	.sub-logos-toggle:hover {
		color: #828282;
		border-color: #828282;
	}

	.sub-logos:not([expanded]) img:nth-of-type(n+5) {
		display: none;
	}

	.opacity-zero {
		opacity: 0;
	}
}
