.peachpay {
	.tooltip {
		position: relative;
		display: inline-flex;
		align-items: center;

		img {
			display: inline-block;
			height: 0.5rem;
			width: 0.5rem;
			border: 1px solid black;
			border-radius: 100%;
			padding: 2px;
			opacity: 0.7;
			transform: rotate(90deg);
		}

		.tooltip-body {
			visibility: hidden;
			width: max-content;
			background-color: black;
			color: white;
			text-align: center;
			padding: 0.5rem 1rem;
			border-radius: 0.4rem;

			/* Position the tooltip text - see examples below! */
			position: absolute;
			z-index: 1000000;

			&::after {
				content: ' ';
				position: absolute;
				margin-left: -5px;
				border-width: 5px;
				border-style: solid;
			}

			&.right {
				left: calc(100% + 5px);

				&::after {
					top: calc(50% - 5px);
					right: 100%;
					border-color: transparent black transparent transparent;
				}
			}

			&.left {
				right: calc(100% + 5px);

				&::after {
					top: calc(50% - 5px);
					right: -10px;
					border-color: transparent transparent transparent black;
				}
			}

			h4 {
				color: white !important;
			}
		}

		&:hover .tooltip-body {
			visibility: visible;
		}
	}
}
