#pp-onboarding-tour-modal {
	position: fixed;
	z-index: 5;
	bottom: 20px;
	right: 80px;
	width: 293px;
	max-width: calc(100% - 88px);
	height: 48px;
	max-height: calc(100% - 180px);
	background: white;
	border: 1px solid #757575;
	border-radius: 4px;

	.pp-onboarding-tour-title,
	.pp-onboarding-tour-description {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 46px;
		padding: 16px;
		cursor: pointer;

		h4 {
			margin: 0;
		}
	}

	.pp-onboarding-tour-title {
		position: sticky;
		top: 0;
		padding: 0 16px;
		border-radius: 4px;
		background: white;
		transition: box-shadow 0.2s ease;
	}

	.onboarding-tour-modal-toggle {
		display: flex;
		align-items: center;
		width: 12px;
		transform: rotate(180deg);
		transition: transform 0.2s ease;
	}

	.pp-onboarding-tour-description {
		padding-top: 0;
		height: auto;
		color: #616161;

		p {
			margin: 0;
		}
	}

	.pp-onboarding-tour-description,
	hr,
	.pp-onboarding-tour-body {
		display: none;
	}

	.pp-onboarding-tour-body {
		padding: 16px 0 8px;
		overflow-x: hidden;

		.pp-onboarding-tour-todo {
			display: flex;
			flex-direction: column;
			padding: 8px 8px 0;
			text-decoration: none;

			.pp-onboarding-tour-todo-header {
				display: flex;
				justify-content: flex-start;
				align-items: center;
				color: black;

				.pp-onboarding-tour-todo-checked {
					display: flex;
					align-items: center;
					width: 18px;
					min-width: 18px;
					height: 18px;
					border: 2px solid #757575;
					border-radius: 2px;

					&.checked {
						border: none;
						width: 20px;
						min-width: 20px;
						height: 20px;

						svg {
							width: 20px;
							height: 20px;

							path {
								fill: #2e7d32;
							}
						}
					}
				}

				.pp-onboarding-tour-todo-title {
					font-size: 15px;
					margin: 0 0 0 8px;
				}
			}

			.pp-onboarding-tour-todo-description {
				p {
					margin: 6px 0 6px 28px !important;
					color: #616161;
				}
			}

			&:hover {
				background: #e1d3f8;
				cursor: pointer;

				.pp-onboarding-tour-todo-title {
					color: #21105d;
				}
			}
		}
	}

	#pp-onboarding-tour-end {
		display: flex;
		justify-content: flex-end;

		a {
			margin: 8px 8px 0 0;
			color: #616161;
			text-decoration: none;

			&:hover {
				cursor: pointer;
			}
		}
	}

	&.open {
		height: auto;
		overflow-y: auto;

		.pp-onboarding-tour-title {
			border-radius: 0;
		}

		.pp-onboarding-tour-title.scrolled {
			box-shadow: rgb(0 0 0 / 12%) 0 1px 3px, rgb(0 0 0 / 24%) 0 1px 2px;
		}

		.pp-onboarding-tour-description {
			border-bottom: 1px solid #757575;
		}

		.onboarding-tour-modal-toggle {
			transform: rotate(0deg);
		}

		.pp-onboarding-tour-description,
		hr {
			display: block;
		}

		.pp-onboarding-tour-body {
			display: block;
		}
	}
}
